import { swiper, swiperSlide } from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'
import axios from 'axios'  // Êñ∞Â¢ûÔºöÂºïÂÖ•axios
import VueEasyLightbox from 'vue-easy-lightbox'  // ÂºïÂÖ•ÂõæÁâáÊü•ÁúãÁªÑ‰ª∂

export default {
    components: {
        swiper,
        swiperSlide,
        VueEasyLightbox  // Ê≥®ÂÜåÁªÑ‰ª∂
    },
    data() {
        return {
            allSwaggerImages: [],
            swiperOptions: {
                pagination: {
                    el: '.swiper-pagination',
                    clickable: true
                },
                navigation: {
                    nextEl: '.swiper-button-next',
                    prevEl: '.swiper-button-prev'
                },
                loop: true,
                autoplay: false
            },
            phoneButtonHover: false,
            lineButtonHover: false,
            lineIdHover: false,
            currentMediaIndex: 0,
            activeTab: {},
            showVideo: false,
            carImages: [],
            carVideo: [],
            carInfo: {
                dealer: {}
            },
            carEquipments: [],
            carGuarantees: [],
            tabs: [
                { title: 'ËªäËºõÈÖçÂÇô', code: 'car_equipments' },
                // { title: 'ËªäËºõÊèèËø∞', code: 'car_desc' },
                { title: 'ËªäÂïÜ‰ªãÁ¥π', code: 'dealer_intro' },
            ],
            visibleLightbox: false,  // Ê∑ªÂä†ÔºöÊéßÂà∂ÁÅØÁÆ±ÊòæÁ§∫Áä∂ÊÄÅ
            lightboxIndex: 0,        // Ê∑ªÂä†ÔºöÁÅØÁÆ±ÂΩìÂâçÊòæÁ§∫ÁöÑÂõæÁâáÁ¥¢Âºï
        }
    },
    computed: {
        currentImage() {
            return this.allSwaggerImages[this.currentMediaIndex]
        },
        lightboxImages() {
            return this.allSwaggerImages
                .filter(media => media.type === 'image')
                .map(media => media.url);
        }
    },
    mounted() {
        const swiper = this.$refs.mySwiper.swiper;
        swiper.on('slideChange', () => {
            this.currentMediaIndex = swiper.realIndex;
        });

        this.activeTab = this.tabs[0];

        // ÁõëÂê¨tabÂèòÂåñÔºåÂΩìÂàáÊç¢Âà∞ÊèèËø∞tabÊó∂Ë∞ÉÊï¥iframeÈ´òÂ∫¶
        this.$watch('activeTab', (newTab) => {
            if (newTab.code === 'car_desc') {
                this.$nextTick(() => {
                    this.resizeContentFrame('contentFrame');
                });
            } else if (newTab.code === 'dealer_intro') {
                this.$nextTick(() => {
                    this.resizeContentFrame('dealerContentFrame');
                });
            }
        });

        // ÂàùÂßãÂåñÊâÄÊúâcontent-frameÁ±ªÁöÑiframeÁõëÂê¨Âô®
        this.$nextTick(() => {
            this.setupAllContentFrames();
        });

        // Ê∑ªÂä†message‰∫ã‰ª∂ÁõëÂê¨Âô®ÔºåÂ§ÑÁêÜiframeÂèëÊù•ÁöÑÊ∂àÊÅØ
        window.addEventListener('message', this.handleIframeMessage);
    },
    beforeDestroy() {
        // ÁßªÈô§‰∫ã‰ª∂ÁõëÂê¨Âô®
        window.removeEventListener('message', this.handleIframeMessage);
    },
    updated() {
        // ÂΩìÂÜÖÂÆπÊõ¥Êñ∞Êó∂ÔºåÂ¶ÇÊûúÂΩìÂâçÊòØÊèèËø∞tabÔºåÂàôË∞ÉÊï¥iframeÈ´òÂ∫¶
        if (this.activeTab.code === 'car_desc') {
            this.resizeContentFrame('contentFrame');
        } else if (this.activeTab.code === 'dealer_intro') {
            this.resizeContentFrame('dealerContentFrame');
        }
    },
    async created() {
        // È°µÈù¢Âä†ËΩΩÊó∂Ëé∑Âèñ id ÂèÇÊï∞
        const carId = this.$route.params.id
        console.log('ÂΩìÂâçËΩ¶ËæÜ ID:', carId)  // ÂèØÊ†πÊçÆÈúÄÊ±ÇÊõøÊç¢‰∏∫ÂÆûÈôÖ‰∏öÂä°ÈÄªËæëÔºàÂ¶ÇËØ∑Ê±ÇÊï∞ÊçÆÔºâ
        this.fetchCarBaseInfo(carId);  // Ë∞ÉÁî®ËØ∑Ê±ÇÊñπÊ≥ï
        this.fetchCarEquipments(carId);  // Ë∞ÉÁî®ËØ∑Ê±ÇÊñπÊ≥ï
        this.fetchCarGuarantees(carId);
        await this.fetchCarImages(carId);
        await this.fetchCarVideo(carId);
        this.initAllSwaggerImages();
    },
    methods: {
        initAllSwaggerImages() {
            let result = [];
            if (this.carVideo && this.carVideo.length > 0) {
                result.push({
                    url: this.carVideo[0],
                    type: this.isYoutubeVideo(this.carVideo[0]) ? 'youtube' : 'video'
                });
            }
            this.allSwaggerImages = result.concat(this.carImages.map(img => {
                return {
                    url: img,
                    type: 'image'
                }
            }));
        },
        handleIframeMessage(event) {
            // Â§ÑÁêÜÊù•Ëá™iframeÁöÑÊ∂àÊÅØ
            if (event.data && (event.data.type === 'iframe-loaded' || event.data.type === 'iframe-resized')) {
                // Êü•ÊâæÊâÄÊúâiframeÔºåÁ°ÆÂÆöÊòØÂì™‰∏™ÂèëÈÄÅÁöÑÊ∂àÊÅØ
                const iframes = document.querySelectorAll('iframe.content-frame');
                iframes.forEach(iframe => {
                    try {
                        if (iframe.contentWindow === event.source) {
                            // Êõ¥Êñ∞ÂØπÂ∫îiframeÁöÑÈ´òÂ∫¶
                            iframe.style.height = `${event.data.height}px`;
                        }
                    } catch (e) {
                        console.error('Â§ÑÁêÜiframeÊ∂àÊÅØÂá∫Èîô', e);
                    }
                });
            }
        },
        setupAllContentFrames() {
            // Ëé∑ÂèñÈ°µÈù¢‰∏äÊâÄÊúâcontent-frameÁ±ªÁöÑiframe
            const contentFrames = document.querySelectorAll('iframe.content-frame');
            contentFrames.forEach(frame => {
                frame.onload = () => {
                    try {
                        // ËÆæÁΩÆiframeÈ´òÂ∫¶‰∏∫ÂÖ∂ÂÜÖÂÆπÈ´òÂ∫¶
                        frame.style.height = (frame.contentWindow.document.body.scrollHeight + 50) + 'px';

                        // ËÆæÁΩÆiframeÂÜÖÂÆπÂèòÂåñÁöÑÁõëÂê¨
                        const resizeObserver = new ResizeObserver(entries => {
                            for (let entry of entries) {
                                if (entry.target === frame.contentWindow.document.body) {
                                    frame.style.height = (entry.target.scrollHeight + 50) + 'px';
                                }
                            }
                        });

                        try {
                            // Â∞ùËØïÁõëÂê¨iframeÂÜÖÂÆπÂèòÂåñ
                            resizeObserver.observe(frame.contentWindow.document.body);
                        } catch (e) {
                            console.warn('Êó†Ê≥ïÁõëÂê¨iframeÂÜÖÂÆπÂèòÂåñ', e);
                        }
                    } catch (e) {
                        console.error('Ë∞ÉÊï¥iframeÈ´òÂ∫¶Â§±Ë¥•', e);
                    }
                };

                // Â¶ÇÊûúiframeÂ∑≤ÁªèÂä†ËΩΩÂÆåÊàêÔºåÊâãÂä®Ëß¶Âèëonload‰∫ã‰ª∂
                if (frame.contentDocument && frame.contentDocument.readyState === 'complete') {
                    frame.onload();
                }
            });
        },
        resizeContentFrame(frameRef) {
            // Á≠âÂæÖiframeÂä†ËΩΩÂÆåÊàêÂêéË∞ÉÊï¥È´òÂ∫¶
            const frame = this.$refs[frameRef];
            if (frame) {
                frame.onload = () => {
                    try {
                        const frameBody = frame.contentWindow.document.body;
                        const frameHeight = frameBody.scrollHeight;
                        frame.style.height = frameHeight + 'px';
                    } catch (e) {
                        console.error('Ë∞ÉÊï¥iframeÈ´òÂ∫¶Â§±Ë¥•', e);
                    }
                };
            }
        },
        getHtmlContent(content) {
            // ÂáÜÂ§á‰∏Ä‰∏™ÂÆåÊï¥ÁöÑHTMLÊñáÊ°£ÁªìÊûÑÔºåÂåÖÂê´Ê†∑ÂºèÈöîÁ¶ª
            return `
                <!DOCTYPE html>
                <html>
                <head>
                    <meta charset="utf-8">
                    <meta name="viewport" content="width=device-width, initial-scale=1">
                    <style>
                        body {
                            font-family: Arial, sans-serif;
                            line-height: 1.6;
                            color: #333;
                            padding: 0;
                            margin: 0;
                        }
                        img {
                            max-width: 100%;
                            height: auto;
                        }
                        table {
                            border-collapse: collapse;
                            width: 100%;
                        }
                        td, th {
                            border: 1px solid #ddd;
                            padding: 8px;
                        }
                    </style>
                    <script>
                        // Âú®Âä†ËΩΩÂÆåÊàêÂêéÈÄöÁü•Áà∂È°µÈù¢Êõ¥Êñ∞È´òÂ∫¶
                        window.addEventListener('load', function() {
                            // ÈÄöÁü•Áà∂È°µÈù¢Â∑≤Âä†ËΩΩÂÆåÊàê
                            if(window.parent && window.parent.postMessage) {
                                window.parent.postMessage({
                                    type: 'iframe-loaded',
                                    height: document.body.scrollHeight
                                }, '*');
                            }
                            
                            // ÁõëÂê¨ÂÜÖÂÆπÂèòÂåñ
                            const observer = new MutationObserver(function() {
                                if(window.parent && window.parent.postMessage) {
                                    window.parent.postMessage({
                                        type: 'iframe-resized',
                                        height: document.body.scrollHeight
                                    }, '*');
                                }
                            });
                            
                            // ÁõëÂê¨bodyÁöÑÊâÄÊúâÂèòÂåñÔºåÂåÖÊã¨Â≠êÂÖÉÁ¥†
                            observer.observe(document.body, {
                                attributes: true,
                                childList: true,
                                subtree: true
                            });
                            
                            // ÁõëÂê¨ÂõæÁâáÂä†ËΩΩÂÆåÊàê‰∫ã‰ª∂
                            document.querySelectorAll('img').forEach(img => {
                                img.addEventListener('load', function() {
                                    if(window.parent && window.parent.postMessage) {
                                        window.parent.postMessage({
                                            type: 'iframe-resized',
                                            height: document.body.scrollHeight
                                        }, '*');
                                    }
                                });
                            });
                        });
                    </script>
                </head>
                <body>
                    ${content || ''}
                </body>
                </html>
            `;
        },
        async fetchCarImages(carId) {
            let _this = this;
            try {
                var response = await axios.get(`/api/api/car/image/${carId}`)  // ÊõøÊç¢‰∏∫ÂÆûÈôÖÂêéÁ´ØÊé•Âè£Âú∞ÂùÄ
                _this.carImages = response.data.data;
                console.log('Ëé∑ÂèñËΩ¶ËæÜÂõæÁâáÊàêÂäü:', _this.carImages);
            }
            catch (error) {
                console.error('Ëé∑ÂèñËΩ¶ËæÜÂõæÁâáÂ§±Ë¥•:', error)
                // ÂèØÊ∑ªÂä†ÈîôËØØÊèêÁ§∫ÈÄªËæëÔºàÂ¶ÇÊòæÁ§∫ÈîôËØØ‰ø°ÊÅØÔºâ
            }
        },
        async fetchCarVideo(carId) {
            let _this = this;
            try {
                var response = await axios.get(`/api/api/car/video/${carId}`)  // ÊõøÊç¢‰∏∫ÂÆûÈôÖÂêéÁ´ØÊé•Âè£Âú∞ÂùÄ
                _this.carVideo = response.data.data;
                console.log('Ëé∑ÂèñËΩ¶ËæÜËßÜÈ¢ëÊàêÂäü:', _this.carVideo);
            }
            catch (error) {
                console.error('Ëé∑ÂèñËΩ¶ËæÜËßÜÈ¢ëÂ§±Ë¥•:', error)
                // ÂèØÊ∑ªÂä†ÈîôËØØÊèêÁ§∫ÈÄªËæëÔºàÂ¶ÇÊòæÁ§∫ÈîôËØØ‰ø°ÊÅØÔºâ
            }
        },
        fetchCarEquipments(carId) {
            let _this = this;
            axios.get(`/api/api/car/equipment/${carId}`)  // ÊõøÊç¢‰∏∫ÂÆûÈôÖÂêéÁ´ØÊé•Âè£Âú∞ÂùÄ
                .then(response => {
                    // ÊàêÂäüËé∑ÂèñÊï∞ÊçÆÂêéËµãÂÄºÁªôdataÂ±ûÊÄß
                    // this.carInfo = response.data.carInfo
                    _this.carEquipments = response.data.data;
                    console.log('Ëé∑ÂèñËΩ¶ËæÜÈÖçÂ§áÊàêÂäü:', _this.carEquipments);
                })
                .catch(error => {
                    console.error('Ëé∑ÂèñËΩ¶ËæÜÈÖçÂ§áÂ§±Ë¥•:', error)
                    // ÂèØÊ∑ªÂä†ÈîôËØØÊèêÁ§∫ÈÄªËæëÔºàÂ¶ÇÊòæÁ§∫ÈîôËØØ‰ø°ÊÅØÔºâ
                })
        },
        // Ëé∑ÂèñÂçñÂÆ∂‰øùËØÅ
        fetchCarGuarantees(carId) {
            let _this = this;
            axios.get(`/api/api/car/guarantee/${carId}`)  // ÊõøÊç¢‰∏∫ÂÆûÈôÖÂêéÁ´ØÊé•Âè£Âú∞ÂùÄ
                .then(response => {
                    // ÊàêÂäüËé∑ÂèñÊï∞ÊçÆÂêéËµãÂÄºÁªôdataÂ±ûÊÄß
                    // this.carInfo = response.data.carInfo
                    _this.carGuarantees = response.data.data;
                    console.log('Ëé∑ÂèñËΩ¶ËæÜÂçñÂÆ∂‰øùËØÅÊàêÂäü:', _this.carGuarantees);
                })
                .catch(error => {
                    console.error('Ëé∑ÂèñËΩ¶ËæÜÂçñÂÆ∂‰øùËØÅÂ§±Ë¥•:', error)
                    // ÂèØÊ∑ªÂä†ÈîôËØØÊèêÁ§∫ÈÄªËæëÔºàÂ¶ÇÊòæÁ§∫ÈîôËØØ‰ø°ÊÅØÔºâ
                })
        },
        // Ëé∑ÂèñÁªèÈîÄÂïÜ‰ø°ÊÅØ
        fetchDealerInfo(garageId) {
            console.log('Ëé∑ÂèñÁªèÈîÄÂïÜ‰ø°ÊÅØ', garageId);
            let _this = this;
            axios.get(`/api/api/car/dealer/${garageId}`)  // ÊõøÊç¢‰∏∫ÂÆûÈôÖÂêéÁ´ØÊé•Âè£Âú∞ÂùÄ
                .then(response => {
                    // ÊàêÂäüËé∑ÂèñÊï∞ÊçÆÂêéËµãÂÄºÁªôdataÂ±ûÊÄß
                    // this.carInfo = response.data.carInfo
                    _this.carInfo.dealer = response.data.data;
                    if (_this.carInfo.dealer.photos && _this.carInfo.dealer.photos.length > 0) {
                        _this.carInfo.dealer.coverImage = _this.carInfo.dealer.photos[0];
                    }
                    console.log('Ëé∑ÂèñÁªèÈîÄÂïÜ‰ø°ÊÅØÊàêÂäü:', _this.carInfo.dealer);
                })
                .catch(error => {
                    console.error('Ëé∑ÂèñÁªèÈîÄÂïÜ‰ø°ÊÅØÂ§±Ë¥•:', error)
                    // ÂèØÊ∑ªÂä†ÈîôËØØÊèêÁ§∫ÈÄªËæëÔºàÂ¶ÇÊòæÁ§∫ÈîôËØØ‰ø°ÊÅØÔºâ
                })
        },
        // Êñ∞Â¢ûÔºöÂÆö‰πâËé∑ÂèñËΩ¶ËæÜËØ¶ÊÉÖÁöÑËØ∑Ê±ÇÊñπÊ≥ï
        fetchCarBaseInfo(carId) {
            let _this = this;
            axios.get(`/api/api/car/baseInfo/${carId}`)  // ÊõøÊç¢‰∏∫ÂÆûÈôÖÂêéÁ´ØÊé•Âè£Âú∞ÂùÄ
                .then(response => {
                    // ÊàêÂäüËé∑ÂèñÊï∞ÊçÆÂêéËµãÂÄºÁªôdataÂ±ûÊÄß
                    // this.carInfo = response.data.carInfo
                    let car = response.data.data;
                    console.log('Ëé∑ÂèñËΩ¶ËæÜËØ¶ÊÉÖÊàêÂäü:', car);
                    _this.carInfo = car;
                    _this.carInfo.specs = [
                        { name: 'ÊâÄÂú®ÂüéÂ∏Ç', value: car.locationName || '--' },
                        { name: 'Âá∫Âª†Êó•Êúü', value: car.manufactureYear || '--' },
                        { name: 'ÈáåÁ®ãÊï∏', value: car.mileage || '--' },
                        { name: 'ÂºïÊìéÁáÉÊñô', value: car.fuelSystem || '--' },
                        { name: 'ÊéíÊ∞£Èáè', value: car.displacement || '--' },
                        { name: 'ËÆäÈÄüÁ≥ªÁµ±', value: car.transmission || '--' },
                        { name: 'ËªäËâ≤', value: car.color || '--' },
                        { name: 'ËªäÈñÄÊï∏', value: car.doorCount || '--' },
                        { name: '‰πòÂùê‰∫∫Êï∏', value: car.passengerCount || '--' },
                        { name: 'Ê±ΩËªäÂìÅÁâå', value: car.brand || '--' },
                        { name: 'Ê±ΩËªäÂûãËôü', value: car.customModel || '--' },
                    ];
                    _this.carInfo.title = car.brand + ' ' + car.customModel + ' ' + car.manufactureYear;
                    _this.carInfo.price = car.salePrice;
                    _this.carInfo.dealer = {

                    };
                    // ËøôÈáåÂ∞ÜÁΩëÈ°µÊ†áÈ¢òËÆæÁΩÆ‰∏∫title
                    document.title = _this.carInfo.title;
                    console.log(_this.carInfo.specs);
                    _this.fetchDealerInfo(carId);
                })
                .catch(error => {
                    console.error('Ëé∑ÂèñËΩ¶ËæÜËØ¶ÊÉÖÂ§±Ë¥•:', error)
                    // ÂèØÊ∑ªÂä†ÈîôËØØÊèêÁ§∫ÈÄªËæëÔºàÂ¶ÇÊòæÁ§∫ÈîôËØØ‰ø°ÊÅØÔºâ
                })
        },
        changeMedia(index) {
            this.currentMediaIndex = index;
            const swiper = this.$refs.mySwiper.swiper;
            console.log('changeMedia', index, swiper);
            swiper.slideTo(index);
        },
        getThumbnailSrc(media) {
            if (media.type === 'youtube') {
                //ËøôÈáåÊîπ‰∏∫È°πÁõÆ‰∏≠ÁöÑyoutube.png
                return require('@/assets/Youtube.png');
            } else if (media.type === 'video') {
                return require('@/assets/video.png');
            }
            return media.url;
        },
        callPhone() {
            location.href = `tel:${this.carInfo.dealer.companyPhone}`;
        },
        contactLine() {
            location.href = `line://ti/p/${this.carInfo.dealer.lineId}`;
            setTimeout(() => {
                location.href = `https://line.me/R/ti/p/${this.carInfo.dealer.lineId}`;
            }, 1000);
        },
        isYoutubeVideo(url) {
            return url && (url.includes('youtube.com') || url.includes('youtu.be'));
        },
        convertToEmbedUrl(url) {
            if (url.includes('youtube.com/watch?v=')) {
                return url.replace('watch?v=', 'embed/');
            }
            if (url.includes('youtu.be/')) {
                return url.replace('youtu.be/', 'youtube.com/embed/');
            }
            return url;
        },
        showLightbox(index) {
            // Âè™‰∏∫ÂõæÁâáÁ±ªÂûãÊâìÂºÄÁÅØÁÆ±ÔºåËßÜÈ¢ë‰∏çÂ§ÑÁêÜ
            const imgIndex = this.getImageOnlyIndex(index);
            if (imgIndex !== -1) {
                this.lightboxIndex = imgIndex;
                this.visibleLightbox = true;
            }
        },
        getImageOnlyIndex(mixedIndex) {
            const mediaItem = this.allSwaggerImages[mixedIndex];
            if (mediaItem.type !== 'image') return -1;

            let count = 0;
            for (let i = 0; i < mixedIndex; i++) {
                if (this.allSwaggerImages[i].type === 'image') {
                    count++;
                }
            }
            return count;
        }
    }
} 